export default {
  path: '/finance',
  name: 'finance',
  component: () => import('@/layouts/BaseLayout'),
  redirect: '/finance/analysis',
  children: [
    {
      path: 'analysis',
      meta: { title: '财务数据分析'},
      component: () => import('@/views/finance/analysis/index'),
    },
    {
      path: 'ozon_order',
      meta: { title: 'ozon订单'},
      component: () => import('@/views/finance/ozon_order/index'),
    },
    {
      path: 'purchase_order',
      meta: { title: '采购单'},
      component: () => import('@/views/finance/purchase_order/index'),
    }
  ],
}