export default {
  path: '/purchaser',
  name: 'purchaser',
  component: () => import('@/layouts/BaseLayout'),
  redirect: '/purchaser/purchase_order',
  children: [
    {
      path: 'purchase_order',
      meta: { title: '采购订单管理'},
      component: () => import('@/views/purchaser/purchase_order/index'),
    },
    {
      path: 'purchase_product',
      meta: { title: '采购商品管理'},
      component: () => import('@/views/purchaser/purchase_product/index'),
    }
  ],
}