export default {
  path: '/packer',
  name: 'packer',
  component: () => import('@/layouts/BaseLayout'),
  redirect: '/packer/stock_in',
  children: [
    {
      path: 'stock_in',
      meta: { title: '采购入库'},
      component: () => import('@/views/packer/stock_in/index'),
    },
    {
      path: 'stock_out',
      meta: { title: '订单出库'},
      component: () => import('@/views/packer/stock_out/index'),
    }
  ]
}