export default {
  path: '/system',
  name: 'system',
  component: () => import('@/layouts/BaseLayout'),
  redirect: '/system/account',
  children: [
    {
      path: 'account',
      meta: { title: '账号管理'},
      component: () => import('@/views/system/account/index'),
    },
    {
      path: 'department',
      meta: { title: '部门管理'},
      component: () => import('@/views/system/department/index'),
    },
    {
      path: 'team',
      meta: { title: '小组管理'},
      component: () => import('@/views/system/team/index'),
    },
    {
      path: 'shop',
      meta: { title: '店铺管理'},
      component: () => import('@/views/system/shop/index'),
    },
    {
      path: 'controller',
      meta: { title: '系统功能'},
      component: () => import('@/views/system/controller/index'),
    }
  ],
}