export default {
  state: () => ({
    id: '',
    username: '',
    email: '',
    telephone_number: '',

    is_active: false,
    is_admin:false,
    is_department_admin: false,
    is_team_admin:false,

    create_time: '',
    last_login_time: '',
    modify_time: ''

  }),
  mutations: {
    setUser(state, item) {
      state.id = item.id;
      state.username = item.username;
      state.email = item.email;
      state.telephone_number = item.telephone_number;

      state.is_active = item.is_active;
      state.is_admin = item.is_admin;
      state.is_department_admin = item.is_department_admin;
      state.is_team_admin = item.is_team_admin;

      state.create_time = item.create_time;
      state.last_login_time = item.last_login_time;
      state.modify_time = item.modify_time;
    }
  }
}