export default {
  path: '/operation',
  name: 'operation',
  component: () => import('@/layouts/BaseLayout'),
  redirect: '/operation/ozon_order',
  children: [
    {
      path: 'ozon_order',
      meta: { title: 'ozon订单管理'},
      component: () => import('@/views/operation/ozon_order/index'),
    },
    {
      path: 'ozon_order_error',
      meta: { title: 'ozon异常订单管理'},
      component: () => import('@/views/operation/ozon_order_error/index'),
    },
    {
      path: 'ozon_order_over_weight',
      meta: { title: 'ozon超重订单管理'},
      component: () => import('@/views/operation/ozon_order_over_weight/index'),
    },
    {
      path: 'ozon_product',
      meta: { title: 'ozon产品管理'},
      component: () => import('@/views/operation/ozon_product/index'),
    },
    {
      path: 'system_product',
      meta: { title: '系统内商品管理'},
      component: () => import('@/views/operation/system_product/index'),
    }
  ],
}